.cars__list {
    display: flex;
    flex-wrap: wrap;
    gap: 28px;
    justify-content: center;
}

.car__box {
    position: relative;
   width: 274px; 
   margin-bottom: 20px;
}

.car__img {
    width: 100%;
    height: 268px;
    object-fit: cover;
    border-radius: 10px;
    margin-bottom: 14px;
}

.icon {
position: absolute;
fill: white;
right: 15px;
top: 15px;
}

.title{ 
    display: flex;
    justify-content: space-between;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 8px;}

.title__model{ 
    display: flex;
}
.model{
    margin-left: 3px;
    color: #3470FF;
}

.short__info{
    margin-bottom: 28px;
    color: rgba(18, 20, 23, 0.5);
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    overflow: hidden;
    text-overflow: ellipsis; 
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.moreBtn {
cursor: pointer;
width: 100%;
height: 44px;
border-radius: 10px;
border: none;
color: white;
background-color: #3470FF;
}

.moreBtn:hover {
    background-color: #0B44CD;
}